body {
  min-height: 100%;
  height: 100%;
}
.content-wrapper {
  background-color: #fff;
  padding-bottom: 119px;
  color: #727171;
}
.content-wrapper .map-pic {
  position: relative;
  margin: 0 auto;
  width: 1130px;
  height: 625px;
}
.content-wrapper .map-pic.map {
  width: 781px;
}
.content-wrapper .map-pic .more {
  width: 154px;
  height: 80px;
  position: absolute;
  top: 750px;
  right: -360px;
}
.content-wrapper .world-data {
  display: inline-block;
  position: absolute;
  top: 45%;
  color: #fff462;
  text-align: center;
}
.content-wrapper .world-data .left {
  margin-left: 140px;
}
.content-wrapper .world-data .left #num1 {
  font-size: 80px;
}
.content-wrapper .world-data .middle {
  margin-left: 280px;
}
.content-wrapper .world-data .middle #num2 {
  font-size: 80px;
}
.content-wrapper .world-data .right {
  margin-left: 290px;
}
.content-wrapper .world-data .right #num3 {
  font-size: 80px;
}
.content-wrapper .world-data p {
  margin-top: 10px;
  display: inline-block;
  font-size: 18px;
  color: #fff;
  text-align: center;
}
.content-wrapper .world-data span {
  font-size: 30px;
  margin-top: 30px;
}
.content-div {
  margin: 0 auto;
  width: 1506px;
  height: 100%;
}
.content-div.campus {
  margin-top: 150px;
  padding-bottom: 80px;
}
.content-div.campus .campus-names {
  height: 100%;
}
.content-div.campus .campus-names .campus-item {
  margin-top: 5px;
  margin-right: 100px;
  width: 296px;
  height: 142px;
  color: #fff;
}
.content-div.campus .campus-names .campus-item h3 {
  font-size: 19px;
  font-weight: bold;
}
.content-div.campus .campus-names .campus-item p {
  margin-top: 5px;
  font-size: 17px;
}
.content-div.campus .campus-area p {
  color: #fff;
  font-size: 28px;
}
.content-div.campus .campus-area img {
  width: 22px;
  height: 32px;
}
.content-div .box-title {
  width: 100%;
  text-align: center;
  font-weight: bold;
  font-size: 22px;
  padding: 50px 0px;
  box-sizing: border-box;
  color: #727171;
  display: inline-block;
}
.content-div .box-title.white {
  color: #fff;
}
.content-div .box-item {
  overflow: hidden;
  width: 100%;
  height: 100%;
}
.content-div .box-item .item {
  position: relative;
  display: inline-block;
  width: 477px;
  height: 710px;
  box-sizing: border-box;
}
.content-div .box-item .item:nth-child(n+2) {
  margin-left: 38px;
}
.content-div .box-item .item:hover .reason-mask {
  height: 100%;
}
.content-div .box-item .reason-mask {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 0;
  overflow: hidden;
  transition: all 0.5s cubic-bezier(0.54, 0.03, 0.43, 0.97);
  background-color: rgba(114, 113, 113, 0.7);
  color: #fff;
  font-size: 14px;
}
.content-div .box-item .reason-mask .bg {
  margin-top: 521px;
  display: inline-block;
  background-color: #ffffff;
  height: 214px;
  width: 100%;
  box-sizing: border-box;
}
.content-div .box-item .reason-mask .bg h3 {
  color: #727171;
  font-size: 23px;
  margin-top: 28px;
}
.content-div .box-item .reason-mask .bg p {
  color: #727171;
  font-size: 14px;
  margin-top: 12px;
}
.content-div .box-item .box {
  width: 640px;
  height: 300px;
}
.content-div .box-item .box h2 {
  font-size: 22px;
}
.content-div .box-item .box p {
  font-size: 18px;
  margin-top: 10px;
}
.content-div .box-item.top {
  border-bottom: 1px solid #727171;
  padding-bottom: 40px;
}
.content-div .box-item.down {
  margin: 40px 0px 65px;
}
.content-div .box-item.down h2 {
  margin-top: 200px;
}
.content-div .box-item.full-img {
  padding-bottom: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.spread-wrapper {
  background-color: #b48dbd;
}
